import React from 'react'
import is from 'is'
import PropTypes from 'prop-types';
//import {Scope} from 'rescope';
import {Scope, Store, addScopableType} from "rescope/dist/index";

const SimpleObjectProto = ({}).constructor;

/**
 * Inheritable ReScope "HOC" (High Order Component)
 *
 * @class Component
 * @desc Parent React Component with store injection in its state
 */
class Component extends React.Component {
    static childContextTypes = {
        rescope: PropTypes.object,
        $stores: PropTypes.object
    }
    static contextTypes      = {
        rescope: PropTypes.object,
        $stores: PropTypes.object
    }
    
    constructor( p, ctx, q ) {
        super(p, ctx, q);
        let scope   =
                p.__scope
                || ctx.rescope;
        this.$scope = scope;
        
        if ( this.$scope && this.$scope.dead ) {
            console.error("ReScoping using dead scope")
            this.$scope = null;
        }
        
        this.$stores = this.$scope && this.$scope.stores;
        if ( this.constructor.use ) {
            this.state = {
                ...this.state,
                ...scope.map(this, this.constructor.use || [], false)// don't bind
            }
        }
        else if ( !this.$scope )
            this.render = () => <div>No Rescope here { super.name }</div>
    }
    
    dispatch( ...argz ) {
        this.$scope && this.$scope.dispatch(...argz)
    }
    
    componentWillMount() {
        if ( this.constructor.use ) {
            this.$scope.bind(this, this.constructor.use || [], false)
        }
    }
    
    componentWillUnmount() {
        this.constructor.use
        && this.$scope.unBind(this, this.constructor.use || []);
        this.$stores = this.$scope = null;
    }
    
    componentWillReceiveProps( np, nc ) {
        let nScope = np.__scope
            || nc.rescope || this.$scope;
        
        if ( nScope != this.$scope ) {
            this.constructor.use && this.$scope.unBind(this, this.constructor.use);
            this.$scope = nScope;
            
            if ( this.$scope && this.$scope.dead ) {
                console.error("ReScoping using dead scope")
                this.$stores = this.$scope = null;
            }
            else {
                this.$stores = this.$scope.stores;
                this.constructor.use && nScope.bind(this, this.constructor.use);
            }
        }
    }
    
    getChildContext() {
        return {
            rescope: this.$scope || this.context.rescope,
            $stores: this.$scope.stores || this.context.$stores
        };
    }
    
    render() {
        return this.props.children || <div/>
    }
};


/**
 * Return a React "HOC" (High Order Component) that :
 *  - Inject & maintain the stores listed baseComponent::use and/or (use) in the instances props.
 *  - Propag (scope) in the returned React Component context
 *
 * @param BaseComponent {React.Component} Base React Component ( default : React.Component )
 * @param scope {ReScope.Scope|function} the propagated Scope where the stores will be searched ( default : the default
 *     ReScope::Scope::scopes.static scope )
 * @param use {array} the list of stores to inject from the current scope
 * @param additionalContext {Object} context to be propagated
 * @returns {ReScopeProvider}
 */
function reScopeProps( ...argz ) {
    let BaseComponent = (!argz[0] || argz[0].prototype instanceof React.Component) && argz.shift(),
        scope         = (!argz[0] || argz[0] instanceof Scope || is.fn(argz[0])) && argz.shift(),
        use           = (!argz[0] || is.array(argz[0]) || argz[0] instanceof SimpleObjectProto) && argz.shift();
    
    if ( !(BaseComponent && BaseComponent.prototype instanceof React.Component) ) {
        return function ( BaseComponent ) {
            return reScopeProps(BaseComponent, scope, use)
        }
    }
    
    
    let provider = reScopeToState(class ReScopePropsProvider extends React.Component {
        static use               = BaseComponent.use;
        static childContextTypes = {
            ...(BaseComponent.contextTypes || {}),
            rescope: PropTypes.object,
            $stores: PropTypes.object
        };
        static contextTypes      = {
            ...(BaseComponent.contextTypes || {}),
            rescope: PropTypes.object,
            $stores: PropTypes.object
        };
        
        
        getChildContext() {
            return this.context;
        }
        
        render() {
            return <BaseComponent { ...this.props }
                                  { ...this.state }
                                  $dispatch={ this.$dispatch }
                                  $stores={ this.$stores }/>
        }
    }, scope, use);
    provider.displayName         = "propsScoped(" + (BaseComponent.displayName || BaseComponent.name) + ")";
    return provider;
}

/**
 * Return a React "HOC" (High Order Component) that :
 *  - Inherit BaseComponent,
 *  - Inject & maintain the stores in BaseComponent::use and/or (use) in the instances state.
 *  - Propag (scope) in the returned React Component context
 *
 *
 * @param BaseComponent {React.Component} Base React Component ( default : React.Component )
 * @param scope {ReScope.Scope|function} the propagated Scope where the stores will be searched
 * @param use {array} the list of stores injected from the current scope
 * @param additionalContext {Object} context to be propagated
 * @returns {ReScopeProvider}
 */
function reScopeToState( ...argz ) {
    let BaseComponent     = (!argz[0] || argz[0].prototype instanceof React.Component) && argz.shift(),
        scope             = (!argz[0] || argz[0] instanceof Scope || is.fn(argz[0])) && argz.shift(),
        use               = (is.array(argz[0])) && argz.shift(),
        stateMap          = !use && (!argz[0] || argz[0] instanceof SimpleObjectProto) && argz.shift(),
        additionalContext = (!argz[0] || argz[0] instanceof SimpleObjectProto) && argz.shift(),
        initialState      = {};
    
    use = [...(BaseComponent.use || []), ...(use || [])];
    stateMap && Scope.stateMapToRefList(stateMap, initialState, use);
    
    additionalContext = additionalContext && Object.keys(additionalContext).reduce(( h, k ) => (h[k] = PropTypes.any, h), {}) || {};
    
    class ReScopeProvider extends BaseComponent {
        static childContextTypes = {
            ...(BaseComponent.childContextTypes || {}),
            ...(additionalContext),
            rescope: PropTypes.object,
            $stores: PropTypes.object
        }
        static contextTypes      = {
            ...(BaseComponent.contextTypes || {}),
            ...(additionalContext),
            rescope: PropTypes.object,
            $stores: PropTypes.object
        }
        static defaultProps      = {
            ...(BaseComponent.defaultProps || {}),
        }
        static displayName       = "stateScoped(" + (BaseComponent.displayName || BaseComponent.name) + ")";
        
        constructor( p, ctx, q ) {
            super(p, ctx, q);
            this.$scope = this.$scope ||
                p.__scope
                || is.fn(scope) && scope(this, p, ctx) || scope
                || ctx.rescope;
            
            if ( this.$scope && this.$scope.dead ) {
                console.error("ReScoping using dead scope")
                this.$scope = null;
            }
            
            this.$scope && is.fn(scope)
            && this.$scope.retain();
            
            this.$stores = this.$scope && this.$scope.stores;
            if ( this.$scope && use.length ) {
                this.state = {
                    ...this.state,
                    ...initialState,
                    ...this.$scope.map(this, use, false)// don't bind now due to SSR
                }
            }
            else if ( !this.$scope )
                this.render = () => <div>No ReScope context in { BaseComponent.name }</div>
            
            this.$dispatch = this.$dispatch.bind(this);
        }
        
        $dispatch( ...argz ) {
            this.$scope && this.$scope.dispatch(...argz)
        }
        
        componentWillMount() {
            if ( use.length ) {
                this.$scope.bind(this, use, false)
            }
            super.componentWillMount && super.componentWillMount()
        }
        
        componentWillUnmount() {
            
            super.componentWillUnmount && super.componentWillUnmount();
            if ( this.$scope && !this.$scope.dead ) {
                use.length && this.$scope.unBind(this, use);
                is.fn(scope) && this.$scope.dispose();
            }
            
            delete this.$stores;
            delete this.$scope;
        }
        
        componentWillReceiveProps( np, nc ) {
            let nScope = np.__scope
                || scope && this.$scope
                || nc.rescope
                || this.$scope;
            
            if ( nScope != this.$scope ) {
                use.length && this.$scope.unBind(this, use);
                this.$scope = nScope;
                
                if ( this.$scope && this.$scope.dead ) {
                    console.error("ReScoping using dead scope")
                    this.$stores = this.$scope = null;
                }
                else {
                    this.$stores = this.$scope.stores;
                    use.length && nScope.bind(this, use);
                }
            }
            super.componentWillReceiveProps && super.componentWillReceiveProps(np, nc);
        }
        
        getChildContext() {
            let ctx = super.getChildContext && super.getChildContext() || {};
            return {
                ...ctx,
                rescope: this.$scope || this.context.rescope,
                $stores: this.$scope.stores || this.context.$stores
            };
        }
    }
    
    return ReScopeProvider;
}

addScopableType(
    ( Comp ) => (Comp && Comp.prototype instanceof React.Component),
    reScopeToState, false, true
)
addScopableType(
    ( Comp ) => (Comp && Comp.prototype instanceof React.Component),
    /**
     * Return a React "HOC" (High Order Component) that :
     *  - Render BaseComponent with new scope that inherit the given scope or context scope
     *
     * @param BaseComponent {React.Component} Base React Component ( default : React.Component )
     * @param storesMap {Object} the propagated Scope where the stores will be searched
     * @param parentScope {Scope} the propagated Scope where the stores will be searched
     * @param parentScopeId {string} the propagated Scope where the stores will be searched
     * @param additionalContext {Object} context to be propagated
     * @returns {*}
     */
    function reScope( ...argz ) {
        let BaseComponent     = (!argz[0] || argz[0].prototype instanceof React.Component) && argz.shift(),
            scoped            = (!argz[0] || argz[0] instanceof SimpleObjectProto && !(argz[0] instanceof Scope)) && argz.shift(),
            parent            = (!argz[0] || argz[0] instanceof Scope) && argz.shift(),
            parentId          = (!argz[0] || is.string(argz[0])) && argz.shift(),
            additionalContext = (!argz[0] || argz[0] instanceof SimpleObjectProto) && argz.shift();
        
        let compName = BaseComponent.displayName || BaseComponent.name;
        
        additionalContext = additionalContext && Object.keys(additionalContext).reduce(( h, k ) => (h[k] = PropTypes.any, h), {}) || {};
        
        class ScopeProvider extends BaseComponent {
            static childContextTypes = {
                ...(BaseComponent.childContextTypes || {}),
                ...(additionalContext),
                rescope: PropTypes.object,
                $stores: PropTypes.object
            }
            static contextTypes      = {
                ...(BaseComponent.contextTypes || {}),
                ...(additionalContext),
                rescope: PropTypes.object,
                $stores: PropTypes.object
            }
            static defaultProps      = {
                ...(BaseComponent.defaultProps || {}),
            }
            static displayName       = "scoped(" + compName + ")";
            
            constructor( p, ctx, q ) {
                let _parent = parent || parentId && Scope.getScope(parentId) || p.__scope || ctx.rescope,
                    $scope  = new Scope(
                        scoped || {},
                        {
                            autoDestroy: true,
                            key        : compName,
                            parent     : _parent
                        }
                    )
                
                super(p, { ...ctx, rescope: $scope, $stores: $scope.stores }, q);
                
                this.$scope = this.$scope || $scope;
                
                if ( !this.$scope ) {
                    if ( this.$scope && this.$scope.dead ) {
                        console.error("Scoping using dead scope parent");
                        this.$scope = null;
                    }
                    
                    this.$scope = new Scope(
                        scoped || {},
                        {
                            autoDestroy: true,
                            key        : compName,
                            parent     : this.$scope
                        }
                    );
                    
                    this.$stores = this.$scope && this.$scope.stores;
                }
                this.$scope.retain();
            }
            
            componentWillUnmount() {
                
                
                super.componentWillUnmount && super.componentWillUnmount();
                this.$scope && this.$scope.dispose();
                delete this.$stores;
                delete this.$scope;
            }
            
            getChildContext() {
                let ctx = super.getChildContext && super.getChildContext() || {};
                return {
                    ...ctx,
                    rescope: this.$scope,
                    $stores: this.$scope.stores
                };
            }
        }
        
        return ScopeProvider;
    }
)

export {
    Component as default,
    Component,
    reScopeProps
};


